import { Service } from "@/services/service"
import { ref } from "vue"
import Prop from "@/apps/GLOBAL_APP/global_prop";
import { toastWarning } from "@/utils/Alerts";

enum LinkageCode {
    MinLinkageCode='min_linkage_code',
    MaxLinkageCode='max_linkage_code',
    SitePrefix='site_prefix'
}

const minCode = ref(parseInt(localStorage.getItem(LinkageCode.MinLinkageCode)||'0'))
const maxCode = ref(parseInt(localStorage.getItem(LinkageCode.MaxLinkageCode)||'0'))
const sitePrefix = ref(localStorage.getItem(LinkageCode.SitePrefix)||'FAC')

export async function initiateLinkageCodes() {
    sitePrefix.value = (await Prop.sitePrefix()) || 'FAC'
    localStorage.setItem(LinkageCode.SitePrefix, sitePrefix.value)
    if (maxCode.value <= minCode.value) {
        const res = await Service.getJson('next_hts_linkage_ids_batch')
        minCode.value = res.min_id
        maxCode.value = res.max_id
        localStorage.setItem(LinkageCode.MinLinkageCode, `${minCode.value}`)
        localStorage.setItem(LinkageCode.MaxLinkageCode, `${maxCode.value}`)
    }
}

export async function getLinkageCode() {
    try {
        await initiateLinkageCodes()
    } catch (e) {
        console.warn(e)
    }
    if (maxCode.value > minCode.value) {
        --maxCode.value
        localStorage.setItem(LinkageCode.MaxLinkageCode, `${maxCode.value}`)
        return `${sitePrefix.value}-ITS-${maxCode.value}`
    }
    toastWarning("Unable to generate linkage code")
    return 'NaN'
}